<template>
  <div id="live">
    <div class="left">
      <h3>热门直播:</h3>
      <div class="anchors">
        <a :href="'https://live.bilibili.com' + item.link" v-for="item in anchors" :key="item.uid">
          <img :src="item.localface" :alt="item.uname">
          <div class="hover">LIVE</div>
          <span :title="item.uname">{{item.uname}}</span>
        </a>
      </div>
    </div>
    <div class="right">
      <h3>热门活动:</h3>
      <a v-for="item in activities" class="activity" :href="item.link" :key="item.id">
        <img :src="item.localimg" :alt="item.title">
      </a>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      anchors: [
        // {uid: '1', uname: '', localface: '', link: ''},
      ],
      activities: [
        // {
        //   id: '',
        //   title: '',
        //   img: '',
        //   link: '',
        //   localimg: '',
        // }
      ],
      flagAct: false,
      flagAnc: false,
    }
  },
  computed: {
    loaded() {
      return this.flagAct && this.flagAnc;
    }
  },
  watch: {
    loaded(cur, old) {
      this.$emit('isloaded', cur);
    }
  },
  created() {
    let activities = sessionStorage.getItem('activities');
    let anchors = sessionStorage.getItem('anchors');
    
    // 获取热门活动
    if(!activities) {
      axios({
        method: 'get',
        url: 'http://127.0.0.1:3000/bili/getactivities',
      })
      .then(res => {
        this.activities = res.data.data;
        sessionStorage.setItem('activities', JSON.stringify(this.activities));

        this.flagAct = true;
      })
      .catch(err => {
        console.log(err); 
      });
    }
    else {
      this.activities = JSON.parse(activities);
      this.flagAct = true;
    }

    // 获取热门直播
    if(!anchors) {
      axios({
        method: 'get',
        url: 'http://127.0.0.1:3000/bili/gethotlist',
      })
      .then(res => {
        this.anchors = res.data.data.list;
        sessionStorage.setItem('anchors', JSON.stringify(this.anchors));

        this.flagAnc = true;
      })
      .catch(err => {
        console.log(err); 
      });
    }
    else {
      this.anchors = JSON.parse(anchors);
      this.flagAnc = true;
    }

  }
}
</script>

<style lang="less" scoped>
  @briefimg: 64px;
  @widthleft: (@briefimg + 20px) * 3 + 20px;
  @widthright: 208px + 20px;
  @widthtotal: @widthleft + @widthright + 20px;

  #live {
    position: absolute;
    padding: 20px 0;
    width: @widthtotal;
    background-color: #fff;
    border-bottom-left-radius: 4px;
    border-bottom-right-radius: 4px;
    box-shadow: 1px 1px 3px rgba(0, 0, 0, .4);
  }
  h3 {
    margin-bottom: 5px;
    height: 14px;
    line-height: 14px;
    font-size: 14px;
    color: #ea759f;
  }
  .left, .right {
    padding-left: 20px;
    float: left;
  }
  .left {
    width: @widthleft;
  }
  .right {
    width: @widthright;
  }

  .anchors {
    height: 195px;
    border-right: 1px solid #eaeaea;
    overflow: hidden;

    .hover {
      position: absolute;
      // left: @briefimg / 4;
      // top: @briefimg / 4;
      // width: @briefimg / 2;
      // height: @briefimg / 2;
      width: @briefimg;
      height: @briefimg;
      border-radius: 50%;
      background-color: rgba(0, 0, 0, .5);
      line-height: @briefimg;
      font-size: 15px;
      color: #fff;
      font-weight: 700;
      text-align: center;
      text-indent: 16px;
      opacity: 0;

      // 过渡效果
      transform: scale(0);
      transition: all .2s cubic-bezier(0.22, 0.58, 0.12, 0.98);

      &:before {
        content: "";
        position: absolute;
        top: 27px;
        left: 8px;
        display: block;
        width: 12px;
        height: 12px;
        background-color: #ea759f;
        border-radius: 50%;
      }
    }

    a{
      position: relative;
      display: inline-block;
      margin-top: 10px;
      margin-right: 17px;
      width: @briefimg;

      &:hover .hover {
        // left: 0;
        // top: 0;
        // width: @briefimg;
        // height: @briefimg;
        // transform: scale(1.1);
        transform: scale(1);
        opacity: 1;
      }

      img {
        float: left;
        width: @briefimg;
        height: @briefimg;
        border-radius: 50%;
        vertical-align: top;
      }

      span {
        display: block;
        float: left;
        margin-top: 3px;
        height: 16px;
        width: @briefimg;
        font-size: 12px;
        line-height: 16px;
        text-align: center;
        overflow: hidden;
      }
    }
  }

  .activity {
    display: block;
    padding: 10px 20px;
    margin-left: -20px;
    width: 208px + 20px * 2;
    height: 80px + 10px * 2;

    &:hover {
      background-color: #e5e9ef;
    }

    img {
      width: 208px;
      height: 80px;
      border-radius: 4px;
    }
  }
</style>



