<template>
  <div id="gamecenter">
    <div class="left">
      <div class="banner">
        <a :href="banner.link">
          <img :src="banner.localimg" :alt="banner.text">
          <span>{{ banner.text }}</span>
          <div class="bg"></div>
        </a>
      </div>

      <div class="brief">
        <a v-for="item in briefs" :href="item.link" :key="item.id" :title="item.text">
          <img :src="item.localimg" :alt="item.text">
          <span>{{ item.text }}</span>
        </a>
      </div>
    </div>

    <div class="right">
      <a class="bbs" href="">游戏论坛</a>
      <div class="list">
        <ul>
          <li v-for="(item, index) in gameList" :key="item.id" @mouseenter="appear(index)" @mouseleave="disappear(index)">
            <a :href="item.link" :title="item.text">{{item.text}}</a>
          </li>
        </ul>
      </div>

      <div class="imgbox" :style="{backgroundImage: 'url(' + imgbox + ')'}"></div>
    </div>

  </div>
</template>

<script>
export default {
  data() {
    return {
      gameList: [
        // {id: '', text: '', link: '', img: '', localimg: ''},
      ],
      banner: {},
      // {text: '', link: '', img: '', localimg: ''},
      briefs: [
        // {id: '', text: '', link: '', img: '', localimg: ''},
      ],
      imgbox: '',
      loaded: false,
    }
  },
  methods: {
    appear(index) {
      this.imgbox = this.gameList[index].localimg;
    },
    disappear(index) {
      this.imgbox = '';
    },
  },
  watch: {
    loaded(cur, old) {
      this.$emit('isloaded', cur);
    },
  },
  created() {
    // 获取游戏列表数据
    let gameData = sessionStorage.getItem('gameData');
    if(!gameData) {
      axios({
        method: 'get',
        url: 'http://127.0.0.1:3000/bili/getgamecenter',
      })
      .then(res => {
        // 获取数据
        this.gameList = res.data.list;
        this.banner = res.data.banner;
        this.briefs = res.data.brief;
        // 保存数据到本地
        sessionStorage.setItem('gameData', JSON.stringify(res.data));

        this.loaded = true;
      })
      .catch(err => {
        console.log(err);       
      });
    }
    else {
      let data = JSON.parse(gameData);
      this.gameList = data.list;
      this.banner = data.banner;
      this.briefs = data.brief;

      this.loaded = true;
    }
  }
}
</script>

<style lang="less" scoped>
  @boxheight: 256px;
  @boxwidth: 466px;
  @leftwidth: 240px;
  @bannerwidth: 240px;
  @bannerheight: 128px;
  @briefimg: 64px;

  #gamecenter {
    position: absolute;
    width: @boxwidth;
    height: @boxheight;
    background-color: #fff;
    border-bottom-left-radius: 4px;
    border-bottom-right-radius: 4px;
    box-shadow: 1px 1px 3px rgba(0, 0, 0, .4);
  }

  .left, .right {
    margin: 12px auto;
    float: left;
  }

  .banner {
    margin: 0 auto;
    width: @bannerwidth;
    height: @bannerheight;

    a {
      position: relative;
      display: block;
      height: 100%;
      width: 100%;

      img {
        width: 100%;
        border-radius: 4px;
      }

      .bg {
        position: absolute;
        left: 0;
        bottom: 0;
        z-index: 2;
        height: 30px;
        width: 100%;
        background-color: rgba(0, 0, 0, .5);
        filter: blur(5px);
      }

      span {
        position: absolute;
        left: 0;
        bottom: 0;
        z-index: 3;
        padding: 0 10px;
        display: block;
        width: 100%;
        height: 30px;
        line-height: 30px;
        font-size: 12px;
        color: #fff;
      }
      
    }
  }

  .brief {
    margin-top: 6px;

    a {
      display: block;
      float: left;
      padding: 6px 12px 0;
      overflow: hidden;

      &:hover {
        background-color: #e5e9ef;
      }

      img {
        width: @briefimg;
        height: @briefimg;
        border-radius: 12px;
        vertical-align: top;
      }

      span {
        display: block;
        margin: 8px auto;
        width: @briefimg;
        text-align: center;
        font-size: 14px;
        overflow: hidden;
        height: 14px;
        line-height: 14px;
      }
    }
  }

  .bbs {
    position: absolute;
    display: block;
    right: 0;
    margin-right: 12px;
    margin-top: 10px;
    color: #00a1d6;
  }

  .right {
    // position: relative;
    width: 201px;
    min-height: 234px;
    background-image: url('../../static/images/line.png');
    background-repeat: no-repeat;
  }

  .imgbox {
    position: absolute;
    left: 460px;
    bottom: 0;
    width: 220px;
    height: 220px;
    // background-color: yellowgreen;
    background-repeat: no-repeat;
  }

  .list {
    padding: 36px 0 0 6px;
    background-image: url('../../static/images/title.png');
    background-repeat: no-repeat;

    ul {
      list-style: none;

      li {
        display: block;
        padding-left: 16px; 
        height: 28px;
        line-height: 28px;

        a {
          display: block;
          padding-right: 16px;
          height: 28px;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
        }

        &:hover {
          background-color: #e5e9ef;
        }
      };
    }
  }
</style>



