<template>
  <div id="login">
    <ul class="list">
      <li class="user">
        <a href="">
          <div class="headimg"><img src="../../static/images/akari.jpg" alt=""></div>
        </a>
      </li>
      <li><a href="">历史</a></li>
    </ul>
  </div>
</template>

<script>
export default {
  
}
</script>

<style lang="less" scoped>
  @menuheight: 42px;
  @menuuser: 36px;

  .user {
    .headimg {
      display: block;
      height: @menuuser;
      width : @menuuser;         
    }

    img {
      height: @menuuser;
      width : @menuuser;
      border-radius: 50%;
      vertical-align: middle;
    }
  }
  .list {
    position: relative;
    height: @menuheight;
    line-height: @menuheight;
    color: #222;
    list-style: none;

    li {
      display: block;
      position: relative;
      float: left;
      height: @menuheight;

      &:hover:not(.user) {
        background-color: hsla(0, 0, 100%, .3);
      }

      .displaybox {
        // display: none;
        position: absolute;
        left: 0;
        top: @menuheight;
        z-index: 9;
      }

      a {
        display: block;
        padding: 0px 8px;
        height: @menuheight;
        font-size: 12px;
        line-height: @menuheight;
        text-decoration: none;
        overflow: hidden;

        &:active, &:visited {
          color: #222;
        }
      }
    }
  }
</style>




