<template>
  <div id="app">
    <Navbar></Navbar>
  </div>
</template>


<script>
  import Navbar from './components/Navbar.vue'
  export default {
    name: 'app',
    data() {
      return {
        msg: 'Welcome to Your Vue.js App'
      }
    },
    components: {
      Navbar,
    }
  }
</script>

<style lang="less">
  * {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
  }
  body {
    overflow: hidden;
  }
  a, a:hover, a:active, a:visited {
    text-decoration: none;
    color: #222;
    font-size: 12px;
  }
</style>


