<template>
  <div id="navbar">
    <div class="navbar">
      <div class="menu">
        <div class="blur"></div>
        <div class="shadow"></div>

        <div class="menu-bar">
          <ul class="list left">
            <li v-for="(item, index) of menus" :key="index" @mouseenter="toggleShow(item)" 
              @mouseleave="toggleShow(item)">
              <a :href="item.href" target="_blank" :title="item.title">
                <i class="icon" v-if="item.icon != ''" :style="{backgroundPosition: item.icon}"></i>
                {{ item.text }}
              </a>
              <transition name="animate-class" v-if="item.children" enter-active-class="animated faster fadeIn" leave-active-class="animated faster fadeOut">
                <div class="displaybox" v-if="item.children" v-show="item.children.show && item.children.loaded" 
                :is="item.children.name" @isloaded="onoff(index, $event)"></div>
              </transition>
            </li>
          </ul>

          <div class="upload right">
            <a href="">投 稿</a>
          </div>   

          <!-- <ul class="list right">
            <li class="user">
              <a href="">
                <div class="headimg"><img src="../../static/images/akari.jpg" alt=""></div>
              </a>
            </li>
            <li><a href="">历史</a></li>
          </ul> -->
          <div class="right">
            <Login></Login>
          </div>

          <div class="content">
            <a href="https://www.bilibili.com" target="_self"></a>
            <Search></Search>
          </div>
        </div>
        <!-- end-menu-bar -->
        
      </div>
      <!-- end-menu -->
    </div>
    <!-- end-navbar -->
    <div class="menubar">
      <Menubar></Menubar>
    </div>
  </div>
</template>

<script>
import Gamecenter from './Gamecenter.vue'
import Search from './Search.vue'
import Login from './Login.vue'
import Live from './Live.vue'
import Menubar from './Menubar'
export default {
  name: 'Navbar',
  data() {
    return {
      menus: [
        {text: '主站', icon: '-919px -87px', title: '主站', href: 'https://www.bilibili.com/'},
        {text: '音频', icon: '', title: '来探索bilibili音乐的世界吧~', href: 'https://www.bilibili.com/audio/home/?type=10'},
        {text: '游戏中心', icon: '', title: '游戏中心', children: {name: 'Gamecenter', loaded: false, show: false}, href: 'https://game.bilibili.com/'},
        {text: '直播', icon: '', title: '直播',children: {name: 'Live', loaded: false, show: false}, href: 'https://live.bilibili.com/'},
        {text: '会员购', icon: ''},
        {text: 'BML', icon: ''},
        {text: '下载APP', icon: '-1367px -1175px'},
      ]
    }
  },
  methods: {
    toggleShow(item) {
      if(item.children) {
        item.children.show = !item.children.show;
      }
    },
    onoff(index, ev) {
      this.menus[index].children.loaded = ev;
    },
  },
  components: {
    Gamecenter,
    Search,
    Login,
    Live,
    Menubar,
  }
}
</script>
<style lang="less" scoped>
  @import '../../node_modules/animate.css/animate.min.css'; // 引入外部动画样式
  @menuheight: 42px;
  @menuwidthmin: 980px; // max: 1160px
  @menuwidthmax: 1160px;
  @menuuser: 36px;
  @iconheight: 16px;
  @iconwidth: 17px;

  @boxheight: 280px;
  @boxwidth: 680px;

  .displaybox {
    position: absolute;
    left: 0;
    top: @menuheight;
    z-index: 9;
    line-height: 0;
  }

  /*动画过渡样式*/
  .fade-enter-active, .fade-leave-active {
    transition: opacity .5s;
  }
  .fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
    opacity: 0;
  }

  .left {
    float: left;
  }
  .right {
    float: right;
  }
  .clear {
    clear: both;
  }
  .icon {
    display: block;
    margin: (@menuheight - @iconheight) / 2 @iconwidth / 4;
    width: @iconwidth;
    height: @iconheight;
    float: left;
    background-image: url('../../static/images/bili_icons.png');
    background-position: -919px -87px;
  }
  /*导航栏样式*/
  // @media screen and (max-width: 1400px) {
  //   .menu-bar {
  //     width: @menuwidthmin;
  //   }
  // }

  .navbar {
    position: relative;
    height: 170px;
    background-image: url('../../static/images/bg_title.png');
    background-repeat: no-repeat;
    background-position: 50% -10px;

    .menu {
      position: relative;
      height: @menuheight;

      .content {
        a {
          position: absolute;
          top: 55px;
          left: 24px;
          width: 220px;
          height: 105px;
          background-image: url('../../static/images/bili_logo_fall.png');
        }

        // .search {
        //   box-sizing: content-box;
        //   position: absolute;
        //   right: 0;
        //   bottom: 20px;
        //   padding: 2px 2px 2px 72px;
        //   width: 268px;
        //   height: 32px;
        //   background-color: rgba(0, 0, 0, .12);
        //   border-radius: 6px;
        //   font-size: 12px;
        // }
      }
    }
    .menu-bar {
      position: relative;
      margin: 0 auto;
      width: @menuwidthmin;
      height: 170px;

      .upload {
        position: relative;
        margin-left: 8px;
        width: 58px;
        line-height: @menuheight;
        text-align: center;
        
        a {
          display: block;
          height: @menuheight + 6;
          background-color: rgb(251, 114, 153);
          border-bottom-left-radius: 6px;
          border-bottom-right-radius: 6px;
          font-size: 14px;
          color: #fff;
        }
      }

      .list {
        position: relative;
        height: @menuheight;
        line-height: @menuheight;
        color: #222;
        list-style: none;

        li {
          display: block;
          position: relative;
          float: left;
          height: @menuheight;

          &:hover:not(.user) {
            background-color: hsla(0, 0, 100%, .3);
          }

          a {
            display: block;
            padding: 0px 8px;
            height: @menuheight;
            font-size: 12px;
            line-height: @menuheight;
            text-decoration: none;
            overflow: hidden;

            &:active, &:visited {
              color: #222;
            }
          }
        }
      }
    }
    @media screen and (min-width: 1400px) {
      /*响应页面宽度*/
      .menu-bar {
        width: @menuwidthmax;
      }
    }
    /*菜单条的样式*/
    .blur {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: @menuheight;
      background-image: url('../../static/images/bg_title.png');
      background-repeat: no-repeat;
      background-position: 50% -10px;

      
      filter: blur(4px);
    }

    .shadow {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: @menuheight;
      background-color: hsla(0, 0, 100%, .4);
      box-shadow: rgba(0, 0, 0, .1);
    }
  }
  
</style>
 


