<template>
  <div id="search">
    <form action="" class="searchform">
      <input class="input-box bg" :placeholder="defaultKeyword" v-model.trim="searchKeyword">
      <button type="submit" :class="{bg: true, submit: true, active: submitIn}" @click.prevent="search"
      @mouseenter="submitIn = true" @mouseleave="submitIn = false"></button>
    </form>
    
    <a class="msg" href="">
      <span>排行榜</span>
    </a>
    
  </div>
</template>

<script>
export default {
  data() {
    return {
      submitIn: false,
      defaultKeyword: '',
      searchKeyword: '',
    }
  },
  methods: {
    search() {
      if(this.searchKeyword) {
        window.open('https://search.bilibili.com/all?keyword=' + this.searchKeyword);
      }
      else {
        window.open('https://search.bilibili.com/all?keyword=' + this.defaultKeyword)
      }
    }
  },
  created() {
    // 获取默认展示词
    axios({
      method: 'get',
      // url: 'https://www.bilibili.com/widget/getSearchDefaultWords',
      url: 'http://127.0.0.1:3000/bili/search',
    })
    .then(res => {
      let resdata = res.data;
      this.defaultKeyword = resdata.data.name;
    })
    .catch(err => {
      this.defaultKeyword = 'search:';
      console.log(err);
    })
  }
}
</script>

<style lang="less" scoped>
  #search {
    box-sizing: content-box;
    position: absolute;
    right: 0;
    bottom: 20px;
    padding: 2px 2px 2px 72px;
    // width: 268px;
    height: 32px;
    background-color: rgba(0, 0, 0, .12);
    border-radius: 4px;
    font-size: 12px;
  }
  .searchform {
    // width: 268px;
    height: 32px;
    border-radius: 4px;
    overflow: hidden;

    &:hover .bg {
      background-color: #fff;
      transition: background-color .5s;
    }
  }
  .bg {
    display: block;
    float: left;
    height: 32px;
    outline: 0;
    background-color: hsla(0, 0, 100%, .88);
  }
  .input-box {
    box-sizing: content-box;
    padding: 0 12px;
    width: 200px;
    border: 0;
  }
  .submit {
    margin: 0;
    padding: 0;
    width: 48px;
    border: 0;
    background-image: url('../../static/images/bili_icons.png');
    background-repeat: no-repeat;
    background-position: -653px -720px;  // hover: -718px -720px
    cursor: pointer;

    &.active {
      background-position: -718px -720px;
    }
  }

  .msg {
    position: absolute;
    top: 2px;
    left: 2px;
    width: 68px;
    height: 32px;
    background-color: hsla(0, 0, 100%, .88);
    border-radius: 4px;
    transition: background-color .5s;
    
    &:hover {
      background-color: #fff;
    }

    span {
      display: block;
      padding-left: 26px;
      height: 32px;
      line-height: 32px;
      color: #f25d8e;
      background-image: url('../../static/images/bili_icons.png');
      background-repeat: no-repeat;
      background-position: -659px -655px;
    }
  }
</style>




